* {-webkit-tap-highlight-color:transparent;-webkit-perspective:none;-webkit-backface-visibility:hidden;-webkit-font-smoothing:subpixel-antialiased;}
body{background:radial-gradient(ellipse at bottom,#424242 0%,#ffffff 100%);color:rgb(80, 80, 80);}
.background{position:fixed;background:radial-gradient(ellipse at bottom,#424242 0%,#ffffff 100%);top:0;left:0;width:100vw;height:100vh;z-index:-10000;}
button:hover{cursor:pointer;}
button:disabled{cursor:not-allowed;}
.centerDiv{width:fit-content;margin:auto;}
.logo{height:50px;margin-left:0px;margin-top:0px;cursor:pointer;}
.logoBig{width:90vh;margin:auto;}
.logoSml{max-width:800px;width:98%;}
.header{position:fixed;top:0px;width:100vw;z-index:100000;background:radial-gradient(ellipse at bottom,#a5a5a5 0%,#ffffff 100%);}

.footer{position:fixed;bottom:0;color: rgb(61, 61, 61);background:radial-gradient(ellipse at bottom,#636363 0%,#c4c4c4 100%);width:100%;height:20px;font-weight:bold;text-align:center;padding:5px 0 5px 0;}

.socBtn{display:inline-block;height:fit-content;width:fit-content;background:none;border:none;padding:0;margin:0 5px 0px 5px;transition:all .2s ease-in-out;}
.socBtn:hover{transform:scale(1.2);}
.socImg{height:32px;}

.pic{width:100%;min-width:200px;border-radius:10px;border:3px outset rgb(65, 65, 65);height:auto;}
.text{width:100%;min-width:200px;border-radius:10px;border:3px outset rgb(65, 65, 65);background:rgba(0, 0, 0, 0.5);color:rgb(214, 214, 214);margin-bottom:6px;}
.textIn{padding:5px;text-align:center;}
.title{width:100%;min-width:200px;border-radius:10px;border:3px outset rgb(65, 65, 65);margin-bottom:6px;background:rgba(0, 0, 0, 0.5);font-size:1.5em;font-weight:bold;text-align:center;color:rgb(210,210,210);text-shadow:2px 2px rgba(0,0,0,0.5), -2px -2px rgba(0,0,0,0.5), -2px 2px rgba(0,0,0,0.5), 2px -2px rgba(0,0,0,0.5);}
.title2{font-size:1em;font-weight:bold;text-align:left;margin-bottom:6px;}
.board{width:96%;max-width:800px;padding-right:12px;margin:auto;margin-bottom:60px;}
.boardWidth{width:100%;}
.flexBoard{display:flex;gap:10px;}
.flexr{display:flex;gap:0px 10px;padding-left:8px;}

.txt1{margin-bottom:6px;}
.txt2{margin-bottom:6px;}

.hr1{width:98%;margin:auto;border-width:3px;border-color:rgb(100, 100, 100);border-radius:30%;}
.legd{width:80%;margin:auto;padding:4px;margin-top:-40px;text-align:center;background:rgba(0, 0, 0, 0.7);border-radius:10px;}
.menuButton{max-width:300px;width:33.33%;font-size:1.2em;height:40px;border:none;background:none;color:rgb(192, 192, 192);font-weight:bold;
    text-shadow:2px 2px 5px rgba(0,0,0,0.5), -2px -2px 5px rgba(0,0,0,0.5), -2px 2px 5px rgba(0,0,0,0.5), 2px -2px 5px rgba(0,0,0,0.5);
    transition: all 0.25s ease-out;
}
.menuButton:hover{
    color:rgb(0, 0, 0);font-size:1.3em;
    text-shadow:2px 2px 5px rgba(0,161,0,0.5), -2px -2px 5px rgba(0,161,0,0.5), -2px 2px 5px rgba(0,161,0,0.5), 2px -2px 5px rgba(0,161,0,0.5);
}
.menuButtonAct{color:rgb(0, 0, 0);font-size:1.3em;
    text-shadow:1px 1px rgba(0,0,0,0.5), -1px -1px rgba(0,0,0,0.5), -1px 1px rgba(0,0,0,0.5), 1px -1px rgba(0,0,0,0.5);   
}

.inputForm{border-radius:10px;}


#TITLE1{height:100vh;display:flex;opacity:1;transition: all 0.5s ease-out;}
#IMAGE1{opacity:1;transition: all 0.5s ease-out;}
.titleGone{height:0px;}

.projectBanner{width:100%;max-width:520px;border-radius:10px;border:2px outset gray;margin-top:4px;margin-bottom:4px;}

.control-arrow{
    opacity:0;
}



.starter{margin-top:140px;height:5px;}


.CGray{color:rgb(50,50,50);}
.COrange{color:rgb(255,187,0);}
.CGreen{color:rgb(0,255,3);}
.CGreen2{color:rgb(9,120,11);}
.CFuchsia{color:fuchsia;}
.CWhite{color:rgb(255,235,235);}
.CRed{color:rgb(227,2,2);}
.CRed2{color:rgb(179,5,5);}
body::-webkit-scrollbar {width:0.5em;}
body::-webkit-scrollbar-track {background-color:rgb(10,10,10);box-shadow:inset 0 0 6px rgba(0, 0, 0, 1);}
body::-webkit-scrollbar-thumb {background-color:rgba(136, 161, 134, 0.7);outline:none;border-radius:10px;}
body::-webkit-scrollbar-thumb:hover {background-color:rgba(30, 255, 0, 0.95);}
body::-webkit-resizer {background-color:none;border:none;outline:none;}
@media all and (-ms-high-contrast:none) {*::-ms-backdrop, textarea { white-space: pre; }}
@media screen and (max-width: 760px) {.starter{margin-top:120px;}}
@media screen and (max-width: 419px) {
    .menuButton{font-size:1.0em;}
    .menuButton:hover{font-size:1.1em;}
    .menuButtonAct{font-size:1.1em;}
    .starter{margin-top:100px;}
}